function QuizApp() {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState<string | null>(null);
  const [showExplanation, setShowExplanation] = useState(false);
  const [score, setScore] = useState(0);
  const [quizCompleted, setQuizCompleted] = useState(false);

  const handleAnswerSelect = (answer: string) => {
    if (selectedAnswer) return;
    setSelectedAnswer(answer);
    setShowExplanation(true);
    if (answer === questions[currentQuestionIndex].correctAnswer) {
      setScore(score + 1);
    }
  };

  const handleNextQuestion = () => {
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
      setSelectedAnswer(null);
      setShowExplanation(false);
    } else {
      setQuizCompleted(true);
    }
  };

  const handleRestart = () => {
    setCurrentQuestionIndex(0);
    setSelectedAnswer(null);
    setShowExplanation(false);
    setScore(0);
    setQuizCompleted(false);
  };

  if (quizCompleted) {
    const wrongAnswers = questions.length - score;
    const percentage = (score / questions.length) * 100;
    let message = "";
    let emoji = "";

    if (percentage === 100) {
      message = "Mkemmelsin akm! Tm sorular doru yantladn! ";
      emoji = "";
    } else if (percentage >= 80) {
      message = "Harikasn bebeim! ok iyi bir sonu! ";
      emoji = "";
    } else if (percentage >= 60) {
      message = "yi i kardn canm! Biraz daha alarak daha da iyi olacaksn! ";
      emoji = "";
    } else {
      message = "zlme akm, birlikte alarak daha iyi olacaz! ";
      emoji = "";
    }

    return (
      <div className="quiz-completed-container">
        <div className="quiz-result-card">
          <h2>Quiz Tamamland! {emoji}</h2>
          <div className="score-details">
            <div className="score-item">
              <span className="score-label">Toplam Soru</span>
              <span className="score-value">{questions.length}</span>
            </div>
            <div className="score-item correct">
              <span className="score-label">Doru</span>
              <span className="score-value">{score}</span>
            </div>
            <div className="score-item wrong">
              <span className="score-label">Yanl</span>
              <span className="score-value">{wrongAnswers}</span>
            </div>
            <div className="score-item percentage">
              <span className="score-label">Baar</span>
              <span className="score-value">%{percentage.toFixed(0)}</span>
            </div>
          </div>
          <p className="result-message">{message}</p>
          <div className="result-actions">
            <button onClick={handleRestart} className="restart-button">
              Yeniden Bala
            </button>
            <Link to="/" className="home-button">
              Ana Sayfaya Dn
            </Link>
          </div>
        </div>
      </div>
    );
  }

  const currentQuestion = questions[currentQuestionIndex];

  return (
    <div className="quiz-container">
      <h2>Soru {currentQuestionIndex + 1} / {questions.length}</h2>
      <p className="question-text">{currentQuestion.text}</p>
      <div className="options-container">
        {currentQuestion.options.map((option, index) => (
          <button
            key={index}
            onClick={() => handleAnswerSelect(option.charAt(0))}
            className={`option-button ${
              selectedAnswer === option.charAt(0)
                ? option.charAt(0) === currentQuestion.correctAnswer
                  ? 'correct'
                  : 'incorrect'
                : ''
            } ${selectedAnswer && option.charAt(0) === currentQuestion.correctAnswer ? 'correct' : ''}`}
            disabled={!!selectedAnswer}
          >
            {option}
          </button>
        ))}
      </div>
      {showExplanation && (
        <div className="explanation-container">
          <p className="explanation">{currentQuestion.explanation}</p>
          <p className="motivational-quote">{currentQuestion.motivationalQuote}</p>
          <button onClick={handleNextQuestion}>
            {currentQuestionIndex === questions.length - 1 ? 'Bitir' : 'Sonraki Soru'}
          </button>
        </div>
      )}
    </div>
  );
}

function App() {
  return (
    <Router basename="/halesinav">
      <div className="App">
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/devlet-toplum-din" element={<QuizApp />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
